
import { defineComponent } from 'vue';

const AlertMessaging = defineComponent({
  data() {
    return {
      alerts: []
    }
  },
  mounted() {
    fetch('https://launchercontent.mojang.com/alertMessaging.json')
      .then(res => res.json())
      .then(data => this.alerts = data.entries)
  }
})

export default AlertMessaging
